import React, { useEffect } from "react";

import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import DataTable from "../components/Tables/DataTable";
import SideBySide from "../components/UI/SideBySide";

import changeTitle from "../utils/change-title";

const BulkSMS = () => {
  useEffect(() => {
    changeTitle("Send SMS");
  }, []);

  return (
    <>
      <section>
        <SideBySide left="Total Active Orders:" right="1781" />
      </section>

      <hr />

      <section>
        <div className="basic-form">
          <form>
            <FormGroup label="Message:">
              <Input textarea rows="4" placeholder="Type message here..." />
            </FormGroup>
            <FormGroup>
              <FormButton>Send Message</FormButton>
            </FormGroup>
          </form>
        </div>
      </section>

      <hr />

      <DataTable
        heading="Orders"
        titles={["Order No", "Date", "Name", "Email", "Phone"]}
      >
        {[
          {
            number: "645654",
            date: "15/10/2020 09:00 AM",
            name: "John Doe",
            email: "jondoe@test.com",
            phone: "0430175160",
          },
          {
            number: "645654",
            date: "15/10/2020 09:00 AM",
            name: "John Doe",
            email: "jondoe@test.com",
            phone: "0430175160",
          },
        ].map((order, index) => {
          return (
            <tr key={Math.random()}>
              <td>{index + 1}</td>
              <td>{order.number}</td>
              <td>{order.date}</td>
              <td>{order.name}</td>
              <td>{order.email}</td>
              <td>{order.phone}</td>
            </tr>
          );
        })}
      </DataTable>
    </>
  );
};

export default BulkSMS;
